.header {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    padding: 0 12px;
    background: #FCFCFC;
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
}

.container{
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    padding: 10px;
}
.wrapper{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.block{
    display: flex;
    align-items: center;
    justify-content: space-between;

    width: 100%;
    max-width: 80px;


    font-size: 16px;
    font-weight: 500;
    line-height: 24px;
    color: #000000;
}

.block span {
    border-bottom: 1px dashed #000000
}